import React from "react";
import Modal from "react-modal";

import "./TaskInfo.scss";
import Input from "../../elements/Input/Input";

const TaskInfo = ({
  modalIsOpen,
  closeModal,
  TaskInfo,
  deadLineDate,
  addDesc,
  renameTask,
}) => {
  Modal.setAppElement("#root");
  const { id, task, desc } = TaskInfo;

  const handleTaskChange = (e) => {
    renameTask(id, e.target.value);
  };

  const handleDescChange = (e) => {
    addDesc(id, e.target.value);
  };

  let taskDate;

  if (deadLineDate) {
    taskDate = new Date(deadLineDate).toLocaleDateString("ru-RU", {
      day: "2-digit",
      month: "2-digit",
      year: "numeric",
    });
  } else {
    taskDate = "  ";
  }

  return (
    <Modal
      portalClassName="task-info"
      className="task-info__modal"
      isOpen={modalIsOpen}
      onRequestClose={closeModal}
      contentLabel="Task Inforamtion"
    >
      <Input
        onChange={handleTaskChange}
        addClass="input--transparent task-info__title"
        value={task}
      />
      <div className="task-info__block">
        <div className="task-info__block-title"></div>
        <textarea
          className="input input--transparent task-info__desc"
          value={desc}
          onChange={handleDescChange}
          placeholder="   ?"
        ></textarea>
      </div>
    </Modal>
  );
};

export default TaskInfo;
